import React, { FC } from "react";
import styled, { css } from "styled-components";

import uuid from "../../utils/uuid";

import StatusIndicator, { IconName, StatusVariants } from "./StatusIndicator";

interface StyledStatusIndicatorBlockProps {
	iconSize?: number
	indicatorMt?: string
	indicatorMr?: string
	indicatorMl?: string
	indicatorMb?: string
}

interface StatusIndicatorBlockProps extends StyledStatusIndicatorBlockProps {
	dataQA: string
	ariaLabel: string
	id?: string
	icon?: IconName
	variant?: StatusVariants
	className?: string
	children?: React.ReactNode
}

const StyledStatusIndicator = styled.div<StyledStatusIndicatorBlockProps>`
	position: relative;
	display: block;
	top: 4px;
	${({ iconSize }) => iconSize
		? css`padding-left: calc(${iconSize}px + 17px)`
		: css`padding-left: 17px`};
	};
`;

const StatusIndicatorOverride = styled(StatusIndicator)<StyledStatusIndicatorBlockProps>`
	${({ indicatorMt }) => indicatorMt && { marginTop: `${indicatorMt}` }}
	${({ indicatorMb }) => indicatorMb && { marginBottom: `${indicatorMb}` }}
	${({ indicatorMr }) => indicatorMr && { marginRight: `${indicatorMr}` }}
	${({ indicatorMl }) => indicatorMl && { marginLeft: `${indicatorMl}` }}
`;

const ChildrenContent = styled.div<StyledStatusIndicatorBlockProps>`
	display: inline-block;
	vertical-align: top;
`;
/** @deprecated this component has been deprecated */
const StatusIndicatorBlock: FC<StatusIndicatorBlockProps> = ({
	indicatorMt,
	indicatorMb,
	indicatorMl,
	indicatorMr,
	id,
	icon,
	ariaLabel,
	variant,
	dataQA,
	children,
	iconSize,
	className,
}) => (
	<StyledStatusIndicator iconSize={iconSize} className={className}>
		<StatusIndicatorOverride
			indicatorMt={indicatorMt}
			indicatorMb={indicatorMb}
			indicatorMl={indicatorMl}
			indicatorMr={indicatorMr}
			ariaLabel={ariaLabel}
			id={id || uuid()}
			variant={variant}
			dataQA={dataQA}
			icon={icon}
			iconSize={iconSize}
		/>
		<ChildrenContent>{ children }</ChildrenContent>
	</StyledStatusIndicator>
);

export default StatusIndicatorBlock;
